import './index.scss';
import { Container, Form, Card, Row, Col } from 'react-bootstrap';
import { useEffect, useState } from 'react';

const Body = () => {
    const [prompt, setPrompt] = useState("");
    const [dataState, setDataState] = useState([])

    useEffect(()=>{
        const item = localStorage.getItem('item');
        if (item) {
            const pa = JSON.parse(item);
            setDataState([...pa])
        }
    }, [])

    const handleChange = (e) => {
        setPrompt({ ...prompt, [e.target.name]: e.target.value.trim() })
    }

    const handleSubmit = (e) => {
        e.preventDefault()

        var myHeaders = new Headers();
        const apiKeyConn = process.env.REACT_APP_APIKEYCONN
        myHeaders.append('Authorization', apiKeyConn);
        myHeaders.append("Content-Type", "application/json")
       
        const data = JSON.stringify(prompt);

        const requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: data,
            redirect: 'follow'
        };

        fetch("https://api.openai.com/v1/engines/text-curie-001/completions", requestOptions)
          .then(response => response.json())
          .then(result => {
            console.log(result)
            const answer = result.choices[0]?.text
            const id = result.id
            const text = prompt

            const box = {id, text, answer}
            setDataState([box, ...dataState])
            
            const a = localStorage.getItem('item')
            if (a != null) {
                const pase = JSON.parse(a)
                pase.unshift(box)
                localStorage.setItem('item', JSON.stringify(pase))
            }
            else {
                const newentry = []
                newentry.unshift(box)
                localStorage.setItem('item', JSON.stringify(newentry))
            }
        })
          .catch(error => console.log('error', error));

    };

    return (
        <>
          <Container style={{ opacity: 0.99 }}>
              <center>
                <div className='text-header text-white py-4'>
                  <h2 className='mt-5 fw-bold'>Fun with AI</h2>
                </div>
              </center>
              <Form className='aiform'>
                <label className='mb-3 text-white'>Enter Prompt: </label>
                <textarea name="prompt" className='mb-3 w-100 p-4 stylearea' onChange={handleChange} required/>
                <center>
                    <button className='btn btn-primary mb-3 mybtn' onClick={handleSubmit}>SUBMIT</button>
                </center>
              </Form>

              <div className='response-section mt-4'>
                {console.log(dataState, 'insidejsx')}
                {dataState.map((q) => {
                    return (
                    <Card className="mb-3 p-2 shadow skr" key={q.id}>
                        <Card.Body>
                          <Row>
                            <Col><h4>Prompt:</h4></Col>
                            <Col><p>{q.text.prompt}</p></Col>
                          </Row>
                          <Row>
                            <Col><h4>Response:</h4></Col>
                            <Col><p><p>{q.answer}</p></p></Col>
                          </Row>                    
                        </Card.Body>
                    </Card>
                    )
                })}
                  
              </div>
          </Container>
        </>

    )
}

export default Body;